import React, { useEffect, useState } from "react";

export default function PlzInput({ onChange }) {
  let [input, setInput] = useState("");
  let entered = input.length > 0;
  let [plz, setPLZ] = useState(null);
  useEffect(() => {
    if (!plz && input.length > 0) {
      fetch("/plz_de.json")
        .then(r => r.json())
        .then(data => setPLZ(data));
    }
  }, [input, plz]);
  if (onChange && plz && input.length === 5) {
    onChange(input);
  }
  console.log(plz && plz.length);
  console.log(input);
  return (
    <>
      <input
        placeholder="Postleitzahlsuche..."
        type="text"
        onChange={el => setInput(el.target.value)}
        value={input}
      />
      <div>
        {JSON.stringify(plz && plz.length)}
        {plz && input.length === 5
          ? plz.find(e => parseInt(input) === e[0]).slice(1)
          : "..."}
      </div>
    </>
  );
}
