import React from "react";
import Map from "../components/Map";
import Layout from "../components/BareLayout";

export default function Karte(props) {
  const addresses = props.data.allAddressesJson.edges.map(n => n.node);
  return (
    <Layout>
      <Map
        initialCenter={[51.07406058399357, 10.692255468750005]}
        addresses={addresses}
      />
    </Layout>
  );
}

export const query = graphql`
  query MyQuery {
    allAddressesJson {
      edges {
        node {
          address_name
          address_street
          address_street_no
          address_city
          address_plz
          address_geo_latitude
          address_geo_longitude
          address_info
        }
      }
    }
  }
`;
